import React from "react";

import './Menu.scss'

import animateScrollTo from "animated-scroll-to";

export default class Menu extends React.Component {

    render() {
        const { isMobile, isFooter, menuItems, menuChanger} = this.props;

        const menuItemsJSX = menuItems.map((menuItem, index) => {
            return (
                <li key={index}
                    onClick=
                        {isMobile ?
                            () => menuChanger(false, () => animateScrollTo( document.querySelector(menuItem.anchor) ) ) :
                            () => animateScrollTo( document.querySelector(menuItem.anchor))
                        }
                    className={`menu__item${isMobile ? ' mobile' : ''}${isFooter ? ' footer' : ''}`}><span>{menuItem.value}</span></li>
            )
        });
        return (
            <nav className={`menu${isMobile ? ' mobile' : ''}`}>
                <ul className={`menu__items${isMobile ? ' mobile' : ''}${isFooter ? ' footer' : ''}`}>
                    {menuItemsJSX}
                </ul>
            </nav>
        )
    }
}