import React from "react";

import './CurrentUser.scss';
export default class CurrentUser extends React.Component {
    constructor(props) {
        super(props);
        this.isMobile = this.props.isMobile;
    }

    render() {
        const user = this.props.user;

        return (
            <div className={`current-user${this.isMobile ? ' mobile' : ''}`}>
                <div className={`current-user__info${this.isMobile ? ' mobile' : ''}`}>
                    <div className={`current-user__name${this.isMobile ? ' mobile' : ''}`}>{user.name}</div>
                    <div className={`current-user__email${this.isMobile ? ' mobile' : ''}`}>{user.email}</div>
                </div>
                <div className={`current-user__photo${this.isMobile ? ' mobile' : ''}`}>
                    <img src={user.photo} alt="user"/>
                </div>
            </div>
        )
    }
}